import React, { useState } from 'react';


function PizzaBurgerOption({ setActiveOption, activeOption }) {
  const clName = "pizza__burger-options burger-options"
  return (
    <div
      className={
        activeOption
        ?
        (clName + " active")
          : clName
      }
      onClick={() => setActiveOption(!activeOption)}>
      <div className="burger-options">
        <div> опции </div>
      </div>
    </div>
  );
}
export default PizzaBurgerOption;