import React from 'react';
import LiComponents from "../../../common/LiComponents";
import { CSSTransition } from "react-transition-group"; 

function PizzaOptions({
  types,
  sizes,
  activType,
  activSize,
  setActiveType,
  setActiveSise,
  activeOption,
}) {
  return (
    <CSSTransition
      in={activeOption}
      timeout={500}
      classNames="transitionOptions"
      // unmountOnExit
      // mountOnEnter
    >
      <div
        className={
          activeOption
            ? "pizza__options options active "
            : "pizza__options options"
        }>
        <ul className="options__type">
          <LiComponents
            li_s={types}
            callback={setActiveType}
            value={activType}
          />
        </ul>
        <ul className="options__size">
          <LiComponents
            li_s={sizes}
            callback={setActiveSise}
            value={activSize}
            text={"см"}
          />
        </ul>
      </div>
    </CSSTransition>
  );
}
export default PizzaOptions;